/*
 * Core: General Layout Style
 * -------------------------
 */
html,
body {
  min-height: 100%; }
  .layout-boxed html, .layout-boxed
  body {
    height: 100%; }

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Source Sans Pro', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 400;
  overflow-x: hidden;
  overflow-y: auto; }

/* Layout */
.wrapper {
  min-height: 100%;
  position: relative;
  overflow: hidden !important; }
  .wrapper:before, .wrapper:after {
    content: " ";
    display: table; }
  .wrapper:after {
    clear: both; }
  .layout-boxed .wrapper {
    max-width: 1250px;
    margin: 0 auto;
    min-height: 100%;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
    position: relative; }

.layout-boxed {
  background: url("/images/boxed-bg.jpg") repeat fixed; }

/*
 * Content Wrapper - contains the main content
 * ```.right-side has been deprecated as of v2.0.0 in favor of .content-wrapper  ```
 */
.content-wrapper,
.right-side,
.main-footer {
  transition: -webkit-transform 0.3s ease-in-out, margin 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, margin 0.3s ease-in-out;
  margin-left: 230px;
  z-index: 820; }
  .layout-top-nav .content-wrapper, .layout-top-nav
  .right-side, .layout-top-nav
  .main-footer {
    margin-left: 0; }
  @media (max-width: 767px) {
    .content-wrapper,
    .right-side,
    .main-footer {
      margin-left: 0; } }
  @media (min-width: 768px) {
    .sidebar-collapse .content-wrapper, .sidebar-collapse
    .right-side, .sidebar-collapse
    .main-footer {
      margin-left: 0; } }
  @media (max-width: 767px) {
    .sidebar-open .content-wrapper, .sidebar-open
    .right-side, .sidebar-open
    .main-footer {
      -webkit-transform: translate(230px, 0);
      transform: translate(230px, 0); } }

.content-wrapper,
.right-side {
  min-height: 100%;
  background-color: #ecf0f5;
  z-index: 800; }

.main-footer {
  background: #fff;
  padding: 15px;
  color: #444;
  border-top: 1px solid #d2d6de; }

/* Fixed layout */
.fixed .main-header,
.fixed .main-sidebar,
.fixed .left-side {
  position: fixed; }

.fixed .main-header {
  top: 0;
  right: 0;
  left: 0; }

.fixed .content-wrapper,
.fixed .right-side {
  padding-top: 50px; }
  @media (max-width: 767px) {
    .fixed .content-wrapper,
    .fixed .right-side {
      padding-top: 100px; } }

.fixed.layout-boxed .wrapper {
  max-width: 100%; }

/* Content */
.content {
  min-height: 250px;
  padding: 15px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px; }
  .content:before, .content:after {
    content: " ";
    display: table; }
  .content:after {
    clear: both; }

/* H1 - H6 font */
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: 'Source Sans Pro', sans-serif; }

/* General Links */
a {
  color: #3c8dbc; }

a:hover,
a:active,
a:focus {
  outline: none;
  text-decoration: none;
  color: #72afd2; }

/* Page Header */
.page-header {
  margin: 10px 0 20px 0;
  font-size: 22px; }
  .page-header > small {
    color: #666;
    display: block;
    margin-top: 5px; }

